		flex-wrap: wrap;
	}

	.coachEvaluation .chooseLabels .son {
		position: relative;
		height: 60rpx;
		margin: 0 30rpx 30rpx 0;
		padding: 0 32rpx;
		background: #ffffff;
		box-sizing: border-box;
		border-radius: 4rpx;
		font-size: 32rpx;
		font-family: Source Han Sans CN, Source Han Sans CN-Regular;
